.nav {
  height: 105px;
  position: fixed;
  background-color: none;
  top: 0;
  left: 0;
  width: 84.3rem;
  z-index: 10;
}

.item {
  display: flex;
  margin-left: 28rem;
  cursor: pointer;
}

.logo button {
  border: none;
  position: relative;
  display: flex;
  float: left;
  margin-left: 2rem;
  margin-top: 1rem;
  font-family: Pass Through;
  font-size: 1.5rem;
  height: 1.56rem;
  width: fit-content;
  display: inline;
}
.logo h2 {
  color: aliceblue;
}
.item .normal {
  position: relative;
  margin-left: 1.87rem;
  border: none;
  color: aliceblue;
  font-size: 20px;
  padding: 1.87rem 0.62rem 0.62rem 0.62rem;
}
.item .normal:hover {
  color: #f0a500;
  transition: 0.5s;
}
.item .btn_act {
  background-color: #f0a500;
  color: aliceblue;
  position: absolute;
  border: none;
  border-radius: 5px;
  padding-left: 25px;
  padding-right: 25px;
  padding-top: 10px;
  padding-bottom: 10px;
  justify-content: right;
  float: right;
  margin-left: 45rem;
  margin-top: 1.5rem;
}
.btn_act:hover {
  background-color: aliceblue;
  color: black;
  transition: 0.5s;
}
.fil {
  background-color: #f0a500;
  padding: 2rem 0.7rem 2rem 0.7rem;
  font-size: 20px;
  color: aliceblue;
  border: none;
}
.fil:hover {
  background-color: aliceblue;
  color: black;
  transition: 0.5s;
}
/*Responsive*/
@media screen and(max-width: 620px) {
  .nav {
    height: 7rem;
    position: fixed;
    background-color: none;
    top: 0;
    left: 0;
    width: 84.3rem;
    z-index: 1;
  }

  .item {
    display: flex;
    margin-left: 31.25rem;
    cursor: pointer;
  }

  .logo button {
    border: none;
    position: relative;
    display: flex;
    float: left;
    margin-left: 2rem;
    margin-top: 1rem;
    font-family: Pass Through;
    font-size: 1.25rem;
    height: 1.56rem;
    width: fit-content;
    display: inline;
  }
  .logo h2 {
    color: aliceblue;
  }
  .item .normal {
    position: relative;
    margin-left: 1.87rem;
    border: none;
    color: aliceblue;
    font-size: 14px;
    padding: 1.87rem 0.62rem 0.62rem 0.62rem;
  }
}
