@import url("https://fonts.googleapis.com/css2?family=Scheherazade+New:wght@400;700&display=swap");
* {
  margin: 0;
  padding: 0;

  font-family: Scheherazade New;
  font-weight: 400;
  background-color: #082032;
}

.contaner {
  width: 100vw;
  max-width: 84.3rem;
  margin: 0 auto;
  margin-top: 7rem;
}
.row {
  width: 100vw;
  display: flex;
}
.col-1 {
  width: 50%;
  justify-content: center;
}
.col-1 img {
  width: 20rem;
  height: 20rem;
  margin-top: 3rem;
  margin-left: 5rem;
  opacity: 10;
  z-index: -1;
}
@media screen and (max-width: 780px) {
  .contaner {
    width: 100vw;

    margin-top: 7rem;
  }
  .row {
    width: 100vw;
    display: flex;
    flex-direction: column-reverse;
    flex-wrap: wrap;
  }
  .col-1 {
    width: 50%;
    justify-content: center;
  }
  .col-1 img {
    width: 10rem;
    height: 10rem;
    margin-top: 1rem;
    margin-left: 55%;

    border-radius: 50%;
    z-index: 10;
  }
}
