.cont3 {
  display: flex;
  justify-content: flex-start;
  top: 5rem;
  width: 84.3rem;
  height: 20rem;
  position: relative;
  background: url("../images/data.jpg");
  background-size: cover;
  background-repeat: no-repeats;
  background-position: 100%;
  object-fit: cover;
  overflow: hidden;
}
.cont3 .all_box {
  background: none;
  display: flex;
  justify-items: center;
  margin-left: 12rem;
  margin-top: 5rem;
}
.cont3 .box {
  border-radius: 10px;
  height: 10rem;
  width: 10rem;
  position: relative;
  margin-left: 4rem;
  display: inline-flexbox;
  background: rgba(255, 255, 255, 0.2);
  justify-items: center;
}
.cont3 .box:hover {
  background-color: #334756;
}

.cont3 .box h2 {
  font-size: 1.5rem;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-weight: 400;
  background: none;
  color: rgb(255, 255, 255);
  display: grid;
  place-content: center;
  margin-top: 2rem;
  padding: 1.25rem 1.25rem;
  max-width: 3rem;
  text-align: center;
  margin-left: 2.5rem;
}
