.form {
  width: 84.3rem;
  max-width: 84.3rem;
  margin: 0 auto;
  margin-top: 7rem;
}
.copyRight {
  color: #ffff;
  text-align: center;
  margin-top: 2rem;
  margin-bottom: 1rem;
}
.row1 {
  width: 84.3rem;
  height: 35rem;
  display: flex;
}
.col3 {
  width: 50%;
  background-color: #060606;
  justify-content: center;
  background: none;
  margin-top: 3rem;
  margin-left: 5rem;
}
.contact_data {
  display: flex inline;
  justify-content: flex-start;
  background: none;
  justify-content: center;

  margin-top: 2rem;
  margin-left: 10rem;
  line-height: 5px;
}
.col3 img {
  display: flex;
  width: 1.5rem;
  height: 1.5rem;
  padding: 0.1rem 0.1rem;
  border-radius: 50%;
  background-color: #fff;
}
.col3 h1 {
  color: #ffff;
  background: none;
  font-size: 1.8rem;
  margin-left: 10rem;
}
.col3 p {
  color: #ffff;
  background: none;
  max-width: 22rem;
  margin-left: 10rem;
}
.col3 h3 {
  color: #ffff;
  margin-left: 10rem;
  margin-top: 2rem;
}
.contact_data p {
  color: rgb(98, 101, 104);
  margin-left: 2rem;
  background: none;
  margin-top: 2rem;
  font-size: 1rem;
}
.col3 h5 {
  color: #ffff;
  margin-left: 2rem;
  background: none;
}
.col4 {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  background: none;
  font-size: 1rem;
  margin-left: 1rem;
  display: grid;

  margin-top: 3rem;
}
.col4 h1 {
  color: #ffff;
  height: 3rem;
  margin-left: 1.5rem;
}
.col4 textarea {
  width: 25rem;
  height: 8rem;
  margin-left: 1.5rem;
  color: #ffff;
  border-radius: 10px;
  border: 1px solid #333333;
  background: #334756;
  text-align-last: auto;
  font-size: 18px;
  direction: none;
  font-weight: 200;
}
.input1 {
  width: 20rem;
  height: rem;
}
.input1 input {
  background: #334756;
  border-radius: 10px;
  color: rgb(255, 255, 255);
  border: 1px solid #333333;
  margin-left: 2rem;
  font-size: 18px;
  text-align: center;
}
.btn7 {
  background-color: #f0a500;
  width: 5rem;
  height: 2.5rem;
  color: #ffffff;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  top: 0;
  border: none;
  border-radius: 5px;
  margin-left: 1.5rem;
  margin-bottom: 2.5rem;
}
.btn7:hover {
  background-color: #fff;
  color: #000000;
}
.social {
  width: 70%;
  height: 2.5rem;
  background-color: #f0a500;
  margin-top: 0.5rem;
  margin-left: 10rem;
  display: flex;
  left: 0;
  justify-content: center;
  border-radius: 15px;
}
.social a {
  background: none;
}
.social a img {
  width: 30px;
  height: 30px;
  margin-left: 1.5rem;
  margin-top: 0.2rem;
}
